'use client';
// components/LfaAnalyzer.tsx
import React, { useRef, useState, useEffect } from "react";

type Rect = { x: number; y: number; w: number; h: number };

function clamp(n: number, min: number, max: number) {
  return Math.max(min, Math.min(max, n));
}

function toGray(r: number, g: number, b: number) {
  // ÌëúÏ§Ä Í∞ÄÏ§ëÏπò ÌöåÏÉâÌôî
  return 0.299 * r + 0.587 * g + 0.114 * b;
}

function sampleMeanGray(
  ctx: CanvasRenderingContext2D,
  rect: Rect
): { mean: number; std: number } {
  const { x, y, w, h } = rect;
  const img = ctx.getImageData(x, y, w, h);
  const data = img.data;
  let sum = 0;
  let sum2 = 0;
  let n = 0;
  for (let i = 0; i < data.length; i += 4) {
    const gray = toGray(data[i], data[i + 1], data[i + 2]);
    sum += gray;
    sum2 += gray * gray;
    n++;
  }
  const mean = sum / n;
  const variance = sum2 / n - mean * mean;
  return { mean, std: Math.sqrt(Math.max(variance, 0)) };
}

function scaleToFit(
  imgW: number,
  imgH: number,
  maxW = 1000,
  maxH = 800
): { w: number; h: number; scale: number } {
  const ratio = Math.min(maxW / imgW, maxH / imgH, 1);
  return { w: Math.round(imgW * ratio), h: Math.round(imgH * ratio), scale: ratio };
}

export default function LfaAnalyzer() {
  const [imageURL, setImageURL] = useState<string | null>(null);
  const [imgDim, setImgDim] = useState<{ w: number; h: number }>({ w: 0, h: 0 });
  const [canvasDim, setCanvasDim] = useState<{ w: number; h: number }>({ w: 0, h: 0 });
  const [dragging, setDragging] = useState<null | { target: "control" | "test"; dx: number; dy: number }>(null);
  const canvasRef = useRef<HTMLCanvasElement | null>(null);
  const containerRef = useRef<HTMLDivElement | null>(null);
  const imgRef = useRef<HTMLImageElement | null>(null);

  // Í∏∞Î≥∏ ROI(Ïª®Ìä∏Î°§/ÌÖåÏä§Ìä∏ ÎùºÏù∏ Î∞ïÏä§) ‚Äî Ïù¥ÎØ∏ÏßÄ ÌÅ¨Í∏∞ Î°úÎî© ÌõÑ Ï§ëÏïô Í∑ºÏ≤òÎ°ú Î∞∞Ïπò
  const [controlRect, setControlRect] = useState<Rect>({ x: 50, y: 100, w: 220, h: 22 });
  const [testRect, setTestRect] = useState<Rect>({ x: 50, y: 160, w: 220, h: 22 });

  const [result, setResult] = useState<{
    verdict: "Positive" | "Negative" | "Invalid" | null;
    controlRatio?: number;
    testRatio?: number;
    confidence?: number;
    detail?: string;
  }>({ verdict: null });

  // Ïù¥ÎØ∏ÏßÄ ÏÑ†ÌÉù
  const onFile = (f?: File) => {
    if (!f) return;
    const url = URL.createObjectURL(f);
    setImageURL(url);
  };

  useEffect(() => {
    if (!imageURL) return;
    const img = new Image();
    img.onload = () => {
      imgRef.current = img;
      setImgDim({ w: img.naturalWidth, h: img.naturalHeight });
      const { w, h } = scaleToFit(img.naturalWidth, img.naturalHeight);
      setCanvasDim({ w, h });
      // ROI Ï¥àÍ∏∞Ìôî(Í∞ÄÏù¥Îìú): Ìè≠ÏùÄ Ïù¥ÎØ∏ÏßÄ Ìè≠Ïùò 60%, ÎÜíÏù¥Îäî 2.2% Ï†ïÎèÑ
      const rw = Math.round(w * 0.6);
      const rh = Math.max(16, Math.round(h * 0.025));
      const x0 = Math.round((w - rw) / 2);
      setControlRect({ x: x0, y: Math.round(h * 0.35), w: rw, h: rh });
      setTestRect({ x: x0, y: Math.round(h * 0.55), w: rw, h: rh });
      // Í∑∏Î¶¨Í∏∞
      draw(img, w, h, { x: x0, y: Math.round(h * 0.35), w: rw, h: rh }, { x: x0, y: Math.round(h * 0.55), w: rw, h: rh });
    };
    img.src = imageURL;
    return () => {
      setResult({ verdict: null });
    };
  }, [imageURL]);

  // Ï∫îÎ≤ÑÏä§Ïóê Ïù¥ÎØ∏ÏßÄ/Ïò§Î≤ÑÎ†àÏù¥ Í∑∏Î¶¨Í∏∞
  const draw = (
    img: HTMLImageElement,
    w: number,
    h: number,
    cr: Rect = controlRect,
    tr: Rect = testRect
  ) => {
    const ctx = canvasRef.current?.getContext("2d");
    if (!ctx) return;
    ctx.clearRect(0, 0, w, h);
    ctx.drawImage(img, 0, 0, w, h);

    // Í∞ÄÏù¥Îìú Ïò§Î≤ÑÎ†àÏù¥
    function strokeRect(r: Rect, color: string) {
      ctx.save();
      ctx.lineWidth = 2;
      ctx.strokeStyle = color;
      ctx.strokeRect(r.x, r.y, r.w, r.h);
      ctx.fillStyle = color + "22";
      ctx.fillRect(r.x, r.y, r.w, r.h);
      ctx.restore();
    }
    strokeRect(cr, "#22c55e"); // control - green
    strokeRect(tr, "#3b82f6"); // test - blue
  };

  // ROIÎ•º ÎìúÎûòÍ∑∏ Ïù¥Îèô
  const startDrag =
    (target: "control" | "test") =>
    (e: React.MouseEvent) => {
      e.preventDefault();
      setDragging({ target, dx: e.clientX, dy: e.clientY });
    };

  const onMove = (e: React.MouseEvent) => {
    if (!dragging) return;
    const dx = e.clientX - dragging.dx;
    const dy = e.clientY - dragging.dy;

    const updateRect = (r: Rect): Rect => {
      const nx = clamp(r.x + dx, 0, canvasDim.w - r.w);
      const ny = clamp(r.y + dy, 0, canvasDim.h - r.h);
      return { ...r, x: nx, y: ny };
    };

    if (dragging.target === "control") {
      const nr = updateRect(controlRect);
      setControlRect(nr);
      setDragging({ ...dragging, dx: e.clientX, dy: e.clientY });
      if (imgRef.current) draw(imgRef.current, canvasDim.w, canvasDim.h, nr, testRect);
    } else {
      const nr = updateRect(testRect);
      setTestRect(nr);
      setDragging({ ...dragging, dx: e.clientX, dy: e.clientY });
      if (imgRef.current) draw(imgRef.current, canvasDim.w, canvasDim.h, controlRect, nr);
    }
  };

  const endMove = () => setDragging(null);

  // ÌåêÎèÖ Ïã§Ìñâ
  const analyze = () => {
    const canvas = canvasRef.current;
    const img = imgRef.current;
    if (!canvas || !img) return;

    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    // ROI ÌèâÍ∑† ÌöåÏÉâÍ∞í(Í∞íÏù¥ ÎÇÆÏùÑÏàòÎ°ù Ïñ¥ÎëêÏõÄ ‚Üí ÏùºÎ∞òÏ†ÅÏù∏ Î∂ÑÌôç/Ï†ÅÏÉâ ÎùºÏù∏ÏùÄ Î∞ùÍ∏∞‚Üì)
    const c = sampleMeanGray(ctx, controlRect);
    const t = sampleMeanGray(ctx, testRect);

    // Î∞∞Í≤ΩÏùÄ ROI ÏúÑ/ÏïÑÎûòÎ°ú ÎèôÏùº ÎÜíÏù¥ Î∞ïÏä§Î•º Ïû°ÏïÑ ÌèâÍ∑†(Í∞ÄÎä•ÌïòÎ©¥ Ïù¥ÎØ∏ÏßÄ Î≤îÏúÑ ÏïàÏóêÏÑúÎßå)
    const bgH = Math.max(12, Math.round(controlRect.h * 1.1));
    const bgPad = 8;
    const ctrlBgY = clamp(controlRect.y - bgH - bgPad, 0, canvasDim.h - bgH);
    const testBgY = clamp(testRect.y + testRect.h + bgPad, 0, canvasDim.h - bgH);
    const ctrlBg: Rect = { x: controlRect.x, y: ctrlBgY, w: controlRect.w, h: bgH };
    const testBg: Rect = { x: testRect.x, y: testBgY, w: testRect.w, h: bgH };

    const cbg = sampleMeanGray(ctx, ctrlBg);
    const tbg = sampleMeanGray(ctx, testBg);

    // Î∞ùÍ∏∞Í∞íÏù¥ ÎÇÆÏùÑÏàòÎ°ù ÎùºÏù∏Ïù¥ ÏßÑÌï® ‚Üí "Î∞∞Í≤Ω/ROI" ÎπÑÏú®Î°ú ÏÑ†Î™ÖÎèÑ ÏÇ∞Ï∂ú
    // ratio > 1.20 Ï†ïÎèÑÎ©¥ "ÎààÏóê ÎùÑÍ≤å Ïñ¥ÎëêÏõÄ(=ÎùºÏù∏ Ï°¥Ïû¨)"Î°ú ÌåêÎã®
    const controlRatio = cbg.mean / Math.max(1e-6, c.mean);
    const testRatio = tbg.mean / Math.max(1e-6, t.mean);

    const CONTROL_THRESH = 1.18;
    const TEST_THRESH = 1.12;

    const controlPresent = controlRatio >= CONTROL_THRESH;
    const testPresent = testRatio >= TEST_THRESH;

    let verdict: "Positive" | "Negative" | "Invalid";
    if (!controlPresent) {
      verdict = "Invalid";
    } else if (testPresent) {
      verdict = "Positive";
    } else {
      verdict = "Negative";
    }

    // Ïã†Î¢∞ÎèÑ Ï∂îÏ†ï(Í∞ÑÎã®): (ratio-1)Ïùò ÌÅ¨Í∏∞ Í∏∞Î∞ò 0~1 Ïä§ÏºÄÏùº
    const confBase =
      verdict === "Invalid"
        ? Math.min(Math.max(controlRatio - CONTROL_THRESH, 0), 0.5) * 2
        : Math.min(Math.max((verdict === "Positive" ? testRatio - TEST_THRESH : CONTROL_THRESH - testRatio), 0), 0.5) * 2;
    const confidence = Math.round(clamp(0.5 + confBase, 0, 1) * 100) / 100;

    setResult({
      verdict,
      controlRatio: Math.round(controlRatio * 1000) / 1000,
      testRatio: Math.round(testRatio * 1000) / 1000,
      confidence,
      detail: `CTRL ratio=${controlRatio.toFixed(3)} / TEST ratio=${testRatio.toFixed(3)} (bg/roi)`,
    });
  };

  return (
    <div className="w-full max-w-3xl mx-auto p-4">
      <h2 className="text-2xl font-semibold mb-3">üì∑ LFA ÌÇ§Ìä∏ ÌåêÎèÖ (ÏïåÎ†àÎ•¥Í∏∞ÏÑ± ÎπÑÏóº)</h2>

      <label className="block">
        <input
          type="file"
          accept="image/*"
          onChange={(e) => onFile(e.target.files?.[0])}
          className="hidden"
        />
        <div className="border-2 border-dashed rounded-2xl p-6 text-center cursor-pointer hover:bg-gray-50">
          {imageURL ? "Îã§Ïãú ÏóÖÎ°úÎìúÌïòÎ†§Î©¥ ÌÅ¥Î¶≠" : "Ïó¨Í∏∞Î•º ÌÅ¥Î¶≠ÌïòÍ±∞ÎÇò Ïù¥ÎØ∏ÏßÄÎ•º ÎìúÎûòÍ∑∏Ìï¥ ÏóÖÎ°úÎìú"}
        </div>
      </label>

      {imageURL && (
        <>
          <div
            ref={containerRef}
            className="relative mt-4 select-none"
            style={{ width: canvasDim.w, height: canvasDim.h }}
            onMouseMove={onMove}
            onMouseUp={endMove}
            onMouseLeave={endMove}
          >
            <canvas
              ref={canvasRef}
              width={canvasDim.w}
              height={canvasDim.h}
              className="rounded-xl shadow"
            />
            {/* ÎìúÎûòÍ∑∏ Ìï∏Îì§: Ïª®Ìä∏Î°§ */}
            <div
              role="button"
              aria-label="Control line ROI"
              title="Control line ROI"
              onMouseDown={startDrag("control")}
              style={{
                position: "absolute",
                left: controlRect.x,
                top: controlRect.y,
                width: controlRect.w,
                height: controlRect.h,
                border: "2px solid #22c55e",
                background: "rgba(34,197,94,0.10)",
                borderRadius: "10px",
                cursor: "move",
              }}
            />
            {/* ÎìúÎûòÍ∑∏ Ìï∏Îì§: ÌÖåÏä§Ìä∏ */}
            <div
              role="button"
              aria-label="Test line ROI"
              title="Test line ROI"
              onMouseDown={startDrag("test")}
              style={{
                position: "absolute",
                left: testRect.x,
                top: testRect.y,
                width: testRect.w,
                height: testRect.h,
                border: "2px solid #3b82f6",
                background: "rgba(59,130,246,0.10)",
                borderRadius: "10px",
                cursor: "move",
              }}
            />
          </div>

          <div className="mt-4 flex gap-3">
            <button
              onClick={() => {
                if (imgRef.current) draw(imgRef.current, canvasDim.w, canvasDim.h);
              }}
              className="px-4 py-2 rounded-xl bg-gray-100 hover:bg-gray-200"
            >
              Í∞ÄÏù¥Îìú Îã§Ïãú Í∑∏Î¶¨Í∏∞
            </button>
            <button
              onClick={analyze}
              className="px-4 py-2 rounded-xl bg-black text-white hover:bg-gray-800"
            >
              ÌåêÎèÖÌïòÍ∏∞
            </button>
          </div>

          {result.verdict && (
            <div className="mt-4 p-4 rounded-2xl border shadow-sm">
              <div className="text-lg">
                Í≤∞Í≥º:{" "}
                <span
                  className={
                    result.verdict === "Positive"
                      ? "text-green-600 font-semibold"
                      : result.verdict === "Negative"
                      ? "text-blue-600 font-semibold"
                      : "text-red-600 font-semibold"
                  }
                >
                  {result.verdict}
                </span>
              </div>
              <div className="text-sm text-gray-600 mt-1">
                {result.detail}
                {typeof result.confidence === "number" && (
                  <> / Confidence ‚âà {Math.round(result.confidence * 100)}%</>
                )}
              </div>
              <div className="text-xs text-gray-500 mt-1">
                ‚Äª Ïª®Ìä∏Î°§ ÎùºÏù∏Ïù¥ ÏïΩÌïòÎ©¥ Î∞ïÏä§Î•º Îçî ÏñáÍ≤å/Ï†ïÌôïÌûà ÎßûÏ∂ò Îí§ Îã§Ïãú ÌåêÎèÖÌïòÏÑ∏Ïöî.
              </div>
            </div>
          )}
        </>
      )}
    </div>
  );
}
